@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;1,400&display=swap);
.btn{transition:all .1s ease-in-out;border:none;border-radius:10px;background-color:var(--background-color);color:#000;padding:5px 10px;font-weight:bold;white-space:nowrap;cursor:pointer}.btn:hover{transform:scale(1.05);background-color:var(--background-color-hover);box-shadow:0 0 var(--box-shadow) var(--background-color-hover)}.btn:active{transform:scale(1);transform:translateY(2px);background-color:var(--background-color-click);--box-shadow: 5px}.btn.btn--success{--background-color: #16a085;--background-color-hover: #189c82;--background-color-click: #158871;color:#fff}.btn.btn--primary{--background-color: #45aaf2;--background-color-hover: #2d98da;--background-color-click: #4b7bec;color:#fff}.btn.btn--danger{--background-color: #fc5c65;--background-color-hover: #eb3b5a;--background-color-click: #ca3853;color:#fff}.btn.btn--warning{--background-color: #fd9644;--background-color-hover: #fa8231;--background-color-click: #e2742b;color:#fff}.btn.btn--small{padding:7px 10px;max-height:2rem}.btn.btn--medium{padding:10px 20px;max-height:2.5rem}.btn.btn--large{padding:15px 40px;max-height:3.5rem}
.entry{border:1px solid var(--gray);border-radius:5px;margin:.5rem 0;padding:.5rem;width:100%}
.form{padding:40px;background-color:#fff;border-radius:10px}.form .form-header{font-weight:bold;margin-bottom:2rem}.form .form-body :nth-child(n){margin-bottom:.5rem}.form label{font-weight:bold}.form .form-button{display:flex;flex-direction:row-reverse;justify-content:flex-start}.form .form-button :nth-child(n+1){margin-left:1rem}
.input{color:var(--gray);border:1px solid var(--gray);border-radius:25px;padding:.7rem 0}.input .input--icon{font-size:1rem;margin-right:1rem;margin-left:1rem}.input .input--entry{border:none;font-size:1rem;background:none;outline:none}@media only screen and (max-width: 670px){.input{border:none;border-radius:50%;width:50px;height:50px;cursor:pointer}.input .input--entry{display:none}.background.popup{width:100vw;height:100vh;background-color:#f0f0f0a1;display:flex;justify-content:center;padding-top:5rem;position:fixed;top:0;left:0;z-index:10;transition:all .3s ease-in-out}.background.popup .input{display:block;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;padding:.7rem 1rem;border-radius:5px;background-color:#fff}.background.popup .input .input--icon{display:none}.background.popup .input .input--entry{display:block}}
.header{display:flex;justify-content:space-between;position:relative;margin-top:2rem;height:2rem;align-items:center}.header .flex{display:flex}.header .header--logo{margin-right:1rem}@media only screen and (max-width: 670px){.header .header--logo{margin-right:0}}
.photo{margin-top:1.5rem;border:none;border-radius:15px;padding:0;position:relative;overflow:hidden;width:min(19rem, 100%);height:-webkit-fit-content;height:-moz-fit-content;height:fit-content}.photo .photo__vignette{border:none;display:none;top:0;left:0;width:100%;height:calc(100% - 4px);border-radius:15px;position:absolute;background-color:#000;opacity:0}.photo .photo__label{display:none;color:#fff;position:absolute;width:90%;overflow:hidden;bottom:1rem;left:1rem;z-index:1}.photo .photo__content{border:none;border-radius:15px;width:100%;display:block}.photo .photo--btn{display:none;position:absolute;top:10px;right:10px}.photo:hover .photo__vignette{display:block;opacity:.4}.photo:hover .photo__label{display:block;font-weight:bold}.photo:hover .photo--btn{display:block}
.app .photo-list{width:100%;display:grid;grid-template-columns:repeat(auto-fit, min(19rem, 100%));grid-gap:1.5rem;justify-content:center;padding:.5rem;margin-top:2rem}.app .form-container{position:fixed;display:flex;justify-content:center;align-items:center;top:0;left:0;width:100vw;height:100vh;background-color:rgba(0,0,0,.452)}.app .form-container .form-content{width:80%;max-width:600px}
:root{--gray: #b8b8b8;--background-color: white;--background-color-hover: #e2e0e0;--background-color-click: #d4d1d1;--box-shadow: 10px}*{padding:0;margin:0;outline:none;box-sizing:border-box}*:focus{outline:none}body{margin:0;min-width:300px;font-family:"Roboto",sans-serif}.container{width:80%;margin:0 auto}
